/* *Network Error* */

.internet-error {
    margin-top: 0;
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: #fff;
    z-index: 50;
    font-size: 20px;
    padding-right: 15px;
    padding-left: 15px;
    pointer-events: none;
}

.internet-error p {
    font-size: 20px;
    line-height: 55px;
    color: #f00;
    margin: 0;
    text-align: center;
    animation: blinkingText 1.2s infinite;
    -webkit-animation: blinkingText 1.2s infinite;
    -moz-animation: blinkingText 1.2s infinite;
    -o-animation: blinkingText 1.2s infinite;
}

@keyframes blinkingText {
    0% {
        color: #000;
    }
    49% {
        color: #000;
    }
    60% {
        color: transparent;
    }
    99% {
        color: transparent;
    }
    100% {
        color: #000;
    }
}

/* *Gallery* */

.grid-container {
    padding: 0 !important;
}

.react-fb-image-grid {
    margin: auto;
}

.react-fb-image-grid * {
    margin: 0;
    box-sizing: border-box;
    padding: 0;
    /* cursor: pointer; */
}

.border {
    border: none;
    border-radius: 0;
}

.background {
    background-size: contain !important;
    background-position: center !important;
    background-repeat: no-repeat !important;

    /* background-size: contain !important;
    background-position: 50% !important;
    background-repeat: no-repeat !important; */
}

.height-one {
    height: 100%;
    width: 100% !important;
}

.height-two {
    height: 65%;
    width: 100% !important;
}

.cover {
    background-color: #222;
    opacity: 0.8;
    position: absolute;
    right: 0;
    top: 0;
    left: 0;
    bottom: 0;
}

.cover-text {
    right: 0;
    left: 0;
    bottom: 0;
    color: white;
    font-size: 1.2em !important;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(0%, -50%);
    -ms-transform: translate(0%, -50%);
    transform: translate(0%, -50%);
    text-align: center;
}

.cover-text > p {
    margin: 0;
    position: absolute;
    font-size: 1.5em !important;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.slide {
    height: 0;
    bottom: 100%;
    transition: 0.4s ease;
    overflow: hidden;
}

.border:hover .slide {
    bottom: 0;
    height: auto;
}

.border:hover .animate-text {
    top: 62%;
}

