/* *Tooltip* */

.tooltip {
    position: inherit !important;
    opacity: 1 !important;
}

.tooltip .tooltiptext {
    visibility: hidden;
    opacity: 0;
    width: 35em;
    background-color: #000000bf;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    margin-left: 0.25em;
    padding: 5px 0;
    /* Position the tooltip */
    position: absolute;
    z-index: 1;
    transition: visibility 0.5s ease-in;
    top: 50;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
}

